.scroll-button
  visibility: hidden
  opacity: 0
  position: fixed
  bottom: 5vw
  right: 5vw
  width: 50px
  height: 50px
  background-color: darken($highlight,10 )
  border-radius: 50%
  cursor: pointer
  z-index: 100
  transition: visibility 300ms, opacity  300ms ease-in-out
  &:hover
    background-color: $highlight
  svg
    height: 100%
    width: 100%
    stroke: $terciary

@media screen and (min-width: 1100px)
  .scroll-button
    right: calc(50% - 500px)
